.photos{
  display: flex;
  &-list{ 
    display: flex;
    width: 100%;
    flex-wrap: wrap;
    justify-content: space-between;
    li {
      width: 30%;
      div{
        img {
          max-width: 100%;
        }
      }
    }
  }
  h4{
    text-transform: capitalize;
    line-height: 1;
  }
}